import React from "react";
import  {BrowserRouter, Route} from "react-router-dom";
import Home from "./Home/Home";
import Listing from "./listing/listingApi";
import CollegeDetail from "./details/collegeDetails";
import PlaceOrder from './booking/placeOrder';
import ViewOrder from './booking/ViewOrderApi';
import Header from './Header';
import Register from './login/registerComponent';
import Login from './login/loginComponent';


const Routing = () =>{
    return(
        <BrowserRouter forceRefresh="true">
            <Header/>
            <Route exact path="/" component={Home}/>
            <Route path="/list/:studyId" component={Listing}/>
            <Route path="/details/:restId" component={CollegeDetail}/>
            <Route path="/placeorder/:restName" component={ PlaceOrder}/>
            <Route path="/vieworder" component={ViewOrder}/>
            <Route path="/register" component={Register}/>
            <Route path="/login" component={Login}/>   
        </BrowserRouter>
    )
}
export default Routing;