import React,{Component} from "react";
import ViewOrderDisplay from './ViewOrderDisplay';
import axios from 'axios';
import Header from '../Header';

const url ="http://uoiapi.herokuapp.com/viewOrder"


class ViewOrder extends Component {
    constructor(props) {
        super(props);

        this.state = {
            orders:''
        }
    }
    render() {
        if(!sessionStorage.getItem('userData')) {
            return(
                <div>
                    <h1>
                        Login first to see the Order
                    </h1>
                </div>
            )
        }
        return (    
            <div className="container">
                <ViewOrderDisplay orderData={this.state.orders}/>
            </div>
        )
    }
    /*
    //get orders

    componentDidMount() {
        axios.get(url).then((res) => {this.setState({orders:res.data})})
    }
    */
    componentDidMount(){
        axios.get(`${url}?email=${sessionStorage.getItem('userData').split(',')[1]}`).then((res) => {this.setState({orders:res.data})})
    }
}

export default ViewOrder;
