import react,{Component} from 'react';
import './placeOrder.css';
import Header from '../Header'

const url = "https://uoiapi.herokuapp.com/courseItem";
const PostUrl = "https://uoiapi.herokuapp.com/placeOrder"


//const url = "http://localhost:2700/collegeCourse"

class PlaceOrder extends Component {
    constructor(props){
        super(props);

        this.state = {
            id:Math.floor(Math.random()*100000),
            details:'',
            amount:0,
            college_name:this.props.match.params.restName,
            name:sessionStorage.getItem('userData')?sessionStorage.getItem('userData').split(',')[0]:'',
            phone:sessionStorage.getItem('userData')?sessionStorage.getItem('userData').split(',')[2]:'',
            email:sessionStorage.getItem('userData')?sessionStorage.getItem('userData').split(',')[1]:'',
            address:'',
            status:'Pending'
        }
    }
    handleChange = (event) => {
        this.setState({[event.target.name]:event.target.value})
    }

    handleSubmit = () => {
        fetch(PostUrl,
            {
                method:'POST',
                headers:{
                    'accept':'application/json',
                    'content-type':'application/json'
                },
                body:JSON.stringify(this.state)
            }
        )
        //.then(this.props.history.push('/viewOrder'))
        .then(console.log("payment gateway"))
    }

    renderItems =  (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <div className="items"  key={item.course_id}>
                        <img src={item.course_image} />
                        <h4>{item.course_name}</h4>
                        <h4>{item.course_type}</h4>
                        <h5>Rs{item.course_price}</h5>
                    </div>
                )
            })
        }else{
            return(
                <img src="/images/loader.gif" alt="loader"/>
            )
        }
    }
    render(){
        if(!sessionStorage.getItem('userData')){
            return(
                <div>
                    <h1>Login first to place booking</h1>
                </div>
            )
        }
        console.log(this.state)
        return(
                <div className="container">
                    <div className="panel panel-info">
                        <div className="panel-heading">
                            <h3>
                                Your order from {this.props.match.params.restName} is below:
                            </h3>
                        </div>
                        <div className="panel-body">
                            <form method="POST" action="https://zompayment.herokuapp.com/paynow">
                                <div className="row">
                                        <div className="col-md-12">
                                            <div className="col-md-6">
                                                <div className="form-group">
                                                    <label>Name</label>
                                                    <input className="form-control" name="name" value={this.state.name}
                                                    onChange={this.handleChange}/>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="form-group">
                                                    <label>EmailId</label>
                                                    <input className="form-control" name="email" value={this.state.email}
                                                    onChange={this.handleChange}/>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="form-group">
                                                    <label>Phone</label>
                                                    <input className="form-control" name="phone" value={this.state.phone}
                                                    onChange={this.handleChange}/>
                                                </div>
                                            </div>
                                            <div className="col-md-6">
                                                <div className="form-group">
                                                    <label>Address</label>
                                                    <input className="form-control" name="address" value={this.state.address}
                                                    onChange={this.handleChange}/>
                                                </div>
                                            </div>
                                        </div>
                                </div>
                                    {this.renderItems(this.state.details)}
                                    <div className="row">
                                        <div className="col-md-12">
                                            <h2>Total Cost is Rs.{this.state.amount}</h2>
                                        </div>
                                    </div>
                                    <button className="btn btn-success" onClick={this.handleSubmit}>
                                                Checkout
                                    </button>
                                
                                </form>
                        
                        </div>
                    </div>
                </div>
        )
    }


    componentDidMount(){
        var studyItem=  sessionStorage.getItem('collegeCourse');
        var orderId = []
        studyItem.split(',').map((item) => {
            orderId.push(parseInt(item))
            return 'ok'
        })
        console.log(orderId)
        
        fetch(url,{
            method:'POST',
            headers:{
                'accept':'application/json',
                'Content-Type':'application/json'
            },
            body:JSON.stringify(orderId)
        })
        .then((res) => res.json())
        .then((data) => 
        {
            var Totalprice = 0;
            data.map((item) => {
                Totalprice = Totalprice+parseInt(item.course_price)
                return 'ok'
            })
            this.setState({details:data,amount:Totalprice})
        }) 
    }
}

export default PlaceOrder;
